.results {
    //@include  default-page-layout();
    height: 100vh;
    width: 66vw;
    display: grid;

    margin-top: 52px;
    row-gap: 44px;
}

.results-posts-container {
    display: grid;
    row-gap: 21px;

    .results-posts-wrapper {
        
        .results-posts-posts {
            display: grid;
            row-gap: 21px;
            justify-content: stretch;
        }
    }
}

.result-post {
    display: grid;
    align-content: space-around;
    row-gap: 21px;

    .result-post-topics > * {
        color: $blue;
        font-size: 14px;
        line-height: 17px;
        font-weight: normal;
    }

    .result-post-title a {
        color: $gray-one;
        font-size: 18px;
        line-height: 22px;
    }

    .result-post-links {
        display: flex;
        
    }
}

.post-link {
    min-height: 70px;
    display: flex;
    flex-basis: 205px;
    margin-right: 37px;

    .post-link-box {
        min-width: 70px;
        background-color: $box-gray;
        margin-right: 16px;
    }
    .post-link-link a {
        color: $gray-one;
        font-size: 14px;
        line-height: 17px; 
    }
}